import React, { Component } from 'react'
import { ToggleButton, ToggleButtonGroup } from 'react-bootstrap'
import Layout from '../components/layout'
import SEO from '../components/seo'
import Machine from '../components/machine'
import uab from 'unique-array-objects'

const products = require('../../data/products').a

class Product extends Component {
  constructor(props) {
    super(props)
    let categories = uab(products.map(p => p.category))
    this.state = {
      categories,
      products,
      value: new Array(categories.length).fill().map((a, i) => i),
    }
  }

  handleChange = e => {
    this.setState({ value: e })
  }

  renderMachines = () => {
    return this.state.products
      .filter(p =>
        this.state.value.includes(this.state.categories.indexOf(p.category))
      )
      .map(p => <Machine data={p} />)
  }

  render() {
    return (
      <Layout>
        <SEO
          title="Packing Machines | Multi Head Weighers and Scales | Cup Filler Collar Type Pouch Packing Machine | All Products | New Tech Engineering"
          keywords={[
            `pouch packing spare parts`,
            `packing machine spare parts names`,
            `types of pouch packing machine`,
            `powder pouch packing machine`,
            `10 Head Multi Weigher`,
            `Multi Head Weigher`,
            `Multi Head Weighers and Scales`,
            `Fully Pneumatic Collar Type Cup Filler Machine`,
            `Pneumatic Packing Machine`,
            `MS,SS Pneumatic Cup Filler Machine`,
            `Cup Filler Collar Type Pouch Packing Machine`,
            `Cup Filler Machines`,
            `Semi Pneumatic Cup Filler Machine `,
            `Fully Automatic Filling Machine`,
            `Automatic Pouch Packing Machine`,
          ]}
          description="Find out the complete range and types of pouch packing machine, MS,SS Pneumatic Cup Filler Machine, Cup Filler Collar Type Pouch Packing Machine, Cup Filler Machines and much more on New Tech Engineering (NewTechPackaging)."
        />
        <div className="container">
          <h1>All Products</h1>
          {console.log(this.state)}
          <div className="product-filters">
            <ToggleButtonGroup
              type="checkbox"
              value={this.state.value}
              onChange={this.handleChange}
            >
              {this.state.categories.map((k, i) => (
                <ToggleButton value={i}>{k}</ToggleButton>
              ))}
            </ToggleButtonGroup>
          </div>
          <div className="container machine-box">{this.renderMachines()}</div>
        </div>
      </Layout>
    )
  }
}
export default Product
